@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
.general-title {
	margin: 1rem;
	color: #f7630c;
	text-align: center;
	font-weight: bold;
	font-family: 'Open Sans', sans-serif;
	font-size: 6rem;
}

@media all and (max-width: 570px) {
	.general-title {
		font-size: 5rem;
	}
}

@media all and (max-width: 480px) {
	.general-title {
		font-size: 4rem;
	}
	.table-title {
		font-size: 25px !important;
	}
}

@media all and (max-width: 400px) {
	.general-title {
		font-size: 3rem;
	}
	.table-title {
		font-size: 20px !important;
	}
}

html,
body {
	background: #18191a;
}

.table {
	width: 95%;
	margin: auto;
}

.input-text {
	max-width: 600px;
	width: 100%;
	margin: auto;
	padding: 20px;
}

.table-title {
	margin: 1rem;
	color: #fce100;
	font-size: 30px;
	font-family: 'Open Sans', sans-serif;
	text-align: center;
}

.text-style {
	display: flex;
	align-items: center;
	justify-content: center;

	color: #18191a;
	font-weight: 600;
	font-size: 1.1rem;
	margin: auto;
	text-align: center;
}

.list-group-item {
	height: auto;
	overflow-y: hidden;
	overflow-x: hidden;
}

